.signupContainer {
    display: flex;
    justify-content: flex-end; /* Aligns to the right */
    align-items: center;
    height: 100vh;
    direction: ltr;
}

.signupBox {
    background-color: rgba(255, 255, 255, 0.9);
    width: 45%;
    padding: 40px;
    margin: 10%;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.signupTitle {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
}
