/* Home Page Styles */
.header {
    background-color: #004d00;
    text-align: center;
    color: white;
    background-image: linear-gradient(to right, #004d00, #0ccb0c);
    padding: 40px 20px;
}

.header h1 {
    font-size: 36px;
    margin-bottom: 10px;
}

.searchBar {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.searchBar input {
    padding: 10px;
    width: 300px;
    border-radius: 5px 0 0 5px;
    border: none;
    text-align: right;
}

.searchButton {
    background-color: #28a745;
    border: none;
    padding: 10px 20px;
    border-radius: 0 5px 5px 0;
    color: white;
    cursor: pointer;
}

.searchButton:hover {
    background-color: #218838;
}

/* Fatwa actions Section Styles */
.fatwaActions {
    display: flex;
    justify-content: center; /* Centers the entire section horizontally */
    gap: 15px; /* Adds spacing between the buttons */
    margin-top: 30px;
    width: 100%; /* Ensures the section takes the full width of the container */
    flex-wrap: wrap; /* Allows wrapping if necessary */
    direction: ltr;
}

.fatwaAction {
    width: 50%;
    max-width: 300px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: medium;
    display: flex;
    justify-content: center; /* Centers content horizontally */
    align-items: center; /* Centers content vertically */
    position: relative; /* Allows dropdown positioning */
}

.fatwaAction button {
    background: none; /* Remove background from button */
    border: none; /* Remove border from button */
    color: inherit; /* Inherit color from parent */
    padding: 0; /* Remove default padding */
}

/* Categories section */
.categoriesDropdown {
    width: 100%;
    top: 100%;
    left: 0;
    z-index: 10;
    text-align: center;
    background-color: #eee;
}

.categoriesDropdown button {
    width: 90%;
    text-align: center;
    padding: 10px 20px;
    margin: 5px auto;
    background-color: white; /* Background color for buttons */
    border-radius: 5px; /* Round the corners of the button */
    border: none; /* Remove the border */
    color: black;
    cursor: pointer;
}

/* Recent Fatwas Section Styles */
.recentFatwasSection {
    direction: ltr;
}
.recentFatwas {
    margin: 40px 20px 20px 20px;
    /* width: 69%; */
}

.fatwaItem {
    background-color: white;
    width: 80%;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
    direction: rtl;
}

.fatwaItem p {
    margin: 5px 0;
}

.fatwaHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.fatwaTitle {
    margin-left: auto; /* Ensures the title stays on the right */
}

.fatwaInfo {
    text-align: left;
}

.btnLink {
    background: none;
    color: #28a745;
    border: none;
    cursor: pointer;
    text-decoration: underline;
}

.searchBar {
    margin-top: 40px;
}

.searchBar input {
    border-radius: 5px;
    width: 40%;
    max-width: 600px;
    background: #eee url("../../assets/icons/search.svg") no-repeat 10px;
    background-size: 24px;
}

.searchBar input::placeholder {
    text-align: center;
}

.suggestionsDropdown button {
    background-color: rgba(238, 238, 238, 0.9);
}

.suggestionsDropdown .noSuggestions {
    background-color: rgba(238, 238, 238, 0.9); 
}
