.profileContainer {
    background-color: rgba(255, 255, 255, 0.7);
}

.profileBox {
    background-color: rgba(255, 255, 255, 0.9);
    width: 45%;
    padding: 40px;
    margin: 10%;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.profileTitle {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
}

.profileButton i {
    margin-left: 8px;
    font-size: 16px;
}