.encyclopediaItem {
    background-color: white;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 16px;
}

.encyclopediaHeader {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 8px;
}

.encyclopediaItemTitle {
    font-size: 1.25rem;
    font-weight: bold;
}

.encyclopediaItemInfo {
    font-size: 0.875rem;
    color: #4A5568;
}

.encyclopediaItem p {
    margin-top: 8px;
    margin-bottom: 8px;
}

.encyclopediaItem a {
    font-size: 0.875rem;
    text-decoration: none;
}

.encyclopediaItem a:hover {
    text-decoration: underline;
}
