.loginContainer {
    display: flex;
    justify-content: flex-end; /* Aligns to the right */
    align-items: center;
    height: 100vh;
    direction: ltr;
}

.loginBox {
    background-color: rgba(255, 255, 255, 0.9);
    width: 45%;
    padding: 40px;
    margin: 10%;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.loginTitle {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
}

.rememberMe {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}
.rememberMeLtr {
    direction: ltr;
    justify-content: flex-start;
}
.rememberMeLtr label input {
    margin-right: 10px;
}
.rememberMeRtl {
    direction: rtl;
    justify-content: flex-start;
}
.rememberMeRtl label input {
    margin-left: 10px;
}

.btnPrimary {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
}

.btnPrimary:hover {
    background-color: #218838;
}

.createAccount {
    margin-top: 20px;
    font-size: 14px;
}
