.chatWidgetContainer {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    z-index: 1000;
}

.chatPopup {
    height: 70vh;
    max-height: 70vh;
    transition: all 0.3s ease;
    overflow: hidden;
    background-color: white;
}

@media (max-width: 768px) {
    .chatPopup {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-height: 100%;
        border-radius: 0;
    }
}

/* Fatwa details page */
.fatwaQuestion, .fatwaAnswer {
    padding: 20px;
    border-radius: 8px;
    box-shadow: 5px 3px 6px rgba(0, 0, 0, 0.1);
}

.fatwaQuestion {
    background-color: rgba(95, 228, 100, 0.5);
}

.fatwaAnswer {
    background-color: rgba(249, 249, 249, 0.7);
}
