
/* Global styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}
.background-image-right-bottom-corner {
    background-image: url('../assets/icons/right-bottom-corner.svg');
    background-repeat: no-repeat;
    position: fixed;
    bottom: -40%;
    right: -68%;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.background-image-left-top-corner {
    background-image: url('../assets/icons/left-top-corner.svg');
    background-repeat: no-repeat;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: -1;
}

/* Form Styles */
.input-group {
    margin-bottom: 15px;
    position: relative;
    direction: rtl;
}

.input-group input {
    direction: ltr;
    width: 100%;
    padding: 10px;
    padding-right: 40px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.input-group .icon {
    position: absolute;
    margin-right: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #888;
}

.input-group select {
    direction: ltr;
    text-align: left;
}

/* RTL Styles */
.rtl-container {
    direction: rtl;
    text-align: right;
}
.rtl-container p {
    direction: rtl;
    text-align: right;
}
.rtl-container .input-group {
    direction: ltr;
}
.rtl-container .input-group input {
    direction: rtl;
}
.rtl-container .input-group .icon {
    margin-left: 10px;
}
.rtl-container input {
    direction: rtl;
    text-align: right;
}
.rtl-container textarea {
    direction: rtl;
    text-align: right;
}
.rtl-container input::placeholder {
    direction: rtl;
    text-align: right;
}

.rtl-container .input-group label {
    direction: rtl;
}

.rtl-container .input-group select {
    direction: rtl;
    text-align: right;
}

.react-datepicker-wrapper {
    display: block !important;
}