.meetingContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #f9fafb;
    min-height: 100vh;
}

.meetingIframeContainer {
    width: 100%;
    max-width: 1200px; /* Limits the iframe width on large screens */
    height: 80vh; /* Sets the iframe height to 80% of the viewport height */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Adds a subtle shadow for depth */
    border-radius: 8px; /* Adds rounded corners */
    overflow: hidden; /* Ensures the iframe is contained within rounded corners */
    background-color: #ffffff; /* Background for the iframe container */
}
