.meetingLinkInput {
    display: inline-block;
    width: 100%; /* Full width */
    max-width: 300px;
    direction: ltr;
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    cursor: pointer;
    padding: 2px 5px;
    font-size: 1rem;
    font-family: 'Arial', sans-serif;
    border: 1px solid #E2E8F0;
    border-radius: 6px;
    color: #2D3748; /* Darker text color for better readability */
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    outline: none; /* Removes the default outline */
}

.meetingLinkInput:hover {
    background-color: #E2E8F0; /* Subtle highlight on hover */
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1); /* Adds a soft shadow */
}

.meetingLinkInput:active, .meetingLinkInput:focus {
    background-color: #CBD5E0; /* Darker background on click or focus */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Slightly stronger shadow */
}
