.navbar {
    padding: 20px 20px;
    position: sticky;
    top: 0;
    z-index: 1000;
    direction: ltr;
}

.navbarContainer {
    display: flex;
    justify-content: space-between; /* Adjusted for better layout */
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.logo img {
    height: 40px;
}

.logo a {
    color: #fff;
    text-decoration: none;
}

.navbarMenu {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.navbarItem {
    margin: 0 20px;
    padding-bottom: 10px;
}

.navbarItem .navbarLink {
    color: #fff;
    text-decoration: none;
    padding: 10px 0;
    font-size: 16px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
    transition: all 0.3s ease;
}

.navbarLinkActiveLink {
    color: #012901;
    border-bottom: 3px solid #012901 !important;
}

.navbarItem .navbarLink:hover {
    color: #012901;
    border-bottom: 2px solid #012901;
}

.navbarItem .navbarLink::after {
    content: '';
    display: block;
    width: 100%;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease;
    transform: translateY(5px); /* Adds space between text and underline */
}

.navbarItem .navbarLink:hover::after {
    border-color: #012901;
}

.navbarUser {
    display: flex;
    align-items: center;
}

.navbarUser .navbarLink {
    color: #fff;
    margin-left: 20px;
    text-decoration: none;
    font-size: 16px;
    display: flex;
    align-items: center;
    transition: color 0.3s ease;
}

.navbarUser .navbarLink:hover {
    color: #012901; /* Darker shade of green */
}

.navbarUser i {
    margin-right: 8px; /* Space between icon and text */
    font-size: 18px; /* Icon size */
    transition: color 0.3s ease;
}

.navbarUser .navbarLink:hover i {
    color: #012901; /* Icon color change on hover */
}

.navbarUserNavbarBtn {
    padding: 8px 16px;
    font-size: 16px;
    border-radius: 20px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.navbarUserNavbarBtnActive {
    background-color: #046504; /* Darker background color */
    color: #fff;
}

.navbarUserNavbarBtnInactive {
    color: #fff;
}

.navbarUserNavbarBtnInactive:hover {
    background-color: #3e8e41;
    color: #fff;
}

.navbarUserNavbarBtnActive:hover {
    background-color: #012901; /* Darker on hover */
    color: #fff;
}

/* Notifications and Profile Dropdowns */
.notificationsWrapper, .profileWrapper {
    position: relative;
}

.iconWithBadge {
    position: relative;
    display: inline-block;
    margin-right: 8px;
}

.notificationsBadge {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    font-size: 0.75rem;
    text-align: center;
    line-height: 15px;
}

.notificationsButton {
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.notificationsDropdown {
    position: absolute;
    top: calc(100% + 10px); /* Keep the original top margin */
    background: rgba(238, 238, 238, 0.9);
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    right: 0;
    width: 250px;
    display: none; /* Hide dropdown by default */
}

/* Add an invisible buffer area */
.notificationsWrapper::before, .profileWrapper::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 10px;
    background: transparent;
    z-index: 999;
}

.notificationsWrapper:hover .notificationsDropdown,
.profileWrapper:hover .profileDropdown,
.notificationsWrapper:hover::before,
.profileWrapper:hover::before {
    display: block; /* Show dropdown and buffer area on hover */
}

.notificationsDropdownItem {
    display: flex;
    align-items: center;
    text-align: right;
    direction: rtl;
    padding: 8px 12px;
    border-bottom: 1px solid #ccc;
    font-size: 0.75rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333;
    text-decoration: none;
}

.notificationStatus {
    margin-left: 8px;
    font-size: 1rem;
}

.notificationUnread {
    color: red;
}

.notificationRead {
    color: green; /* Change color for read notifications */
}

.notificationsDropdownItem:last-child {
    border-bottom: none;
}

.notificationsDropdownItem:hover {
    background-color: #f5f5f5;
}

.profileDropdown {
    position: absolute;
    top: calc(100% + 10px); /* Keep the original top margin */
    background: rgba(238, 238, 238, 0.9);
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    right: 0;
    width: 180px;
    display: none; /* Hide dropdown by default */
}

.profileDropdownItem {
    display: block; /* Ensure items are block-level */
    width: 100%; /* Ensure items take full width of the container */
    padding: 8px 12px;
    text-align: right;
    direction: rtl;
    border-bottom: 1px solid #ccc;
    font-size: 0.9rem;
    color: #333;
    text-decoration: none;
    background-color: transparent; /* Ensure background color is transparent by default */
    transition: background-color 0.3s ease;
}

.profileDropdownItem:last-child {
    border-bottom: none;
}

.profileDropdownItem:hover {
    background-color: #f5f5f5; /* Background color change on hover */
}

.profileDropdownItem button {
    all: unset; /* Remove default button styling */
    display: block; /* Make button a block-level element */
    width: 100%; /* Ensure button takes full width */
    padding: 8px 12px;
    text-align: right;
    direction: rtl;
    font-size: 0.9rem;
    color: #333;
    cursor: pointer;
    background-color: transparent; /* Ensure transparent background */
    transition: background-color 0.3s ease;
}

.profileDropdownItem button:hover {
    background-color: #f5f5f5; /* Background color change on hover */
}

.profileButton i,
.profileDropdownItem i {
    margin-left: 8px; /* Space between icon and text */
    font-size: 16px; /* Adjust icon size as needed */
}
